/* Стили для input */
.myInput {
    display: inline-block;
    width: auto;
    padding: 10px;
    margin: 10px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 6px;
    font-size: 16px;
}

/* Стили для input type checkbox */
.myInput[type="checkbox"] {
    cursor: pointer;
    transform: scale(2); /* Увеличение размера чекбокса */
}