body, html {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
}

.centered-div {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    max-width: 700px;
    margin: 10px;
    word-break: break-word;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 10px 0;
    color: #333;
}

h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    border-bottom: 2px solid #2c3e50;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h2 {
    font-size: 2em;
    font-weight: 600;
    color: #34495e;
    padding-bottom: 8px;
    margin-bottom: 18px;
}

h3 {
    font-size: 1.75em;
    font-weight: 500;
    color: #5d6767;
    padding-bottom: 6px;
    margin-bottom: 16px;
}

h4 {
    font-size: 1.5em;
    font-weight: 500;
    color: #95a5a6;
    margin-bottom: 14px;
}

h5 {
    font-size: 1.25em;
    font-weight: 400;
    color: #bdc3c7;
    margin-bottom: 12px;
}

h6 {
    font-size: 1em;
    font-weight: 400;
    color: #ecf0f1;
    margin-bottom: 10px;
}

hr {
    border: none;
    border-radius: 3px;
    height: 3px;
    background: linear-gradient(to right, #007bff, #00c851);
    margin: 20px 0;
    width: auto;
}

hr.dashed {
    border: none;
    height: 1px;
    background: none;
    border-top: 2px dashed #ccc;
    width: 80%;
    margin: 20px 0;
}

.err {
    color: indianred;
}

.done {
    color: forestgreen;
}

.errInput {
    border: 3px solid indianred;
}

.main-content {
    padding-top: 60px;
}

.spaceBetween {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

span {
    font-family: Arial, sans-serif;
    font-size: 24px;
    color: darkslateblue;
}

.gradient {
    background: linear-gradient(to right, #3c9db9, #a77bfe);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.text-shadow {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

span.clickable {
    cursor: pointer;
}

span.hover-animate:hover {
    color: #ff6347;
    transition: color 0.3s ease-in-out;
}

[data-tooltip] {
    position: relative; /* Относительное позиционирование */
}
[data-tooltip]::after {
    content: attr(data-tooltip); /* Выводим текст */
    position: absolute; /* Абсолютное позиционирование */
    width: 300px; /* Ширина подсказки */
    left: 0; top: 0; /* Положение подсказки */
    background: #a77bfe; /* Синий цвет фона */
    border-radius: 5px;
    color: #fff; /* Цвет текста */
    font-size: 20px;
    text-align: center;
    padding: 0.7em; /* Поля вокруг текста */
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Параметры тени */
    pointer-events: none; /* Подсказка */
    opacity: 0; /* Подсказка невидима */
    transition: 0.75s; /* Время появления подсказки */
}
[data-tooltip]:hover::after {
    opacity: 1; /* Показываем подсказку */
    top: 2em; /* Положение подсказки */
}


.progressbar {
    height: 20px;
    position: relative;
    background: #e7e7e7;
    border-radius: 4px;
    padding: 3px;
    box-shadow: inset 0 -1px 1px rgba(255,255,255,0.3);
}
.progressbar > span {
    display: block;
    height: 100%;
    background-color: rgb(43,194,83);
    background-image: linear-gradient(
            center bottom,
            rgb(43,194,83) 37%,
            rgb(84,240,84) 69%
    );
    box-shadow:
            inset 0 2px 9px  rgba(255,255,255,0.3),
            inset 0 -2px 6px rgba(0,0,0,0.4);
    position: relative;
    overflow: hidden;
}

.progressbar > span:after {
    content: "";
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
    background-image: linear-gradient(
            -45deg,
            rgba(255, 255, 255, .2) 25%,
            transparent 25%,
            transparent 50%,
            rgba(255, 255, 255, .2) 50%,
            rgba(255, 255, 255, .2) 75%,
            transparent 75%,
            transparent
    );
    z-index: 1;
    background-size: 50px 50px;
    animation: move 2s linear infinite;
    overflow: hidden;
}
@keyframes move {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 50px 50px;
    }
}

/* Основные стили для фона плашки */
.consent-banner {
    position: fixed; /* Фиксированное положение */
    bottom: 0;
    left: 0; /* Закрепить слева */
    width: 100%; /* Занять всю ширину экрана */
    padding: 20px; /* Внутренний отступ */
    background-color: #f8d7da; /* Цвет фона (например, светло-красный) */
    color: #721c24; /* Цвет текста (например, темно-красный) */
    text-align: center; /* Центрирование текста */
    z-index: 9999; /* Разместить поверх всех элементов */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Легкая тень */
    font-family: Arial, sans-serif; /* Стиль шрифта */
}

/* Стили для текста внутри плашки */
.consent-banner h2 {
    margin: 0 0 10px 0; /* Убираем отступ сверху и снизу */
    font-size: 1.5em; /* Размер шрифта заголовка */
    font-weight: bold; /* Жирный шрифт */
}

.consent-banner p {
    margin: 0 0 20px 0; /* Отступ снизу */
    font-size: 1em; /* Размер шрифта для параграфа */
}

/* Стили для кнопок */
.consent-banner .consent-button {
    padding: 10px 20px; /* Внутренний отступ */
    margin: 0 10px; /* Отступы между кнопками */
    font-size: 1em; /* Размер шрифта */
    border: none; /* Без границ */
    border-radius: 5px; /* Скругленные углы */
    cursor: pointer; /* Указатель мыши в виде руки */
}

.consent-banner .consent-button.accept {
    background-color: #28a745; /* Зеленый фон для кнопки "Принять" */
    color: white; /* Белый текст */
}

.consent-banner .consent-button.exit {
    background-color: #dc3545; /* Красный фон для кнопки "Выйти" */
    color: white; /* Белый текст */
}

/* Добавляем эффект наведения */
.consent-banner .consent-button:hover {
    opacity: 0.9; /* Легкое затемнение */
}

.q-enter {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
}

.q-enter-active {
    opacity: 1;
    transform: scale(1) translateY(0);
    transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.q-exit {
    opacity: 1;
    transform: scale(1) translateY(0);
}

.q-exit-active {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
    transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}