/* Основной контейнер модального окна */
.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5); /* Более темный фон для большей контрастности */
    z-index: 1000; /* Убедиться, что модальное окно поверх других элементов */
}

/* Контент модального окна */
.modalContent {
    padding: 30px; /* Увеличенный padding для большего пространства */
    background: papayawhip; /* Светлый фон для контента */
    border-radius: 20px; /* Более выраженные скругленные углы */
    min-width: 320px; /* Чуть больше минимальной ширины */
    max-width: 600px; /* Максимальная ширина */
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3); /* Более выраженная тень */
    animation: fadeIn 0.3s ease-out; /* Плавное появление с анимацией */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

/* Анимация для плавного появления модального окна */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9); /* Легкое уменьшение размера */
    }
    to {
        opacity: 1;
        transform: scale(1); /* Нормальный размер */
    }
}

/* Активное состояние модального окна */
.modal.active {
    display: flex;
    align-items: center;
    justify-content: center;
}

