/* Общие стили для select */
.mySel {
    display: inline-block;
    width: 100%;
    max-width: 300px; /* Максимальная ширина */
    padding: 10px; /* Внутренний отступ */
    font-size: 16px; /* Размер шрифта */
    font-family: Arial, sans-serif; /* Семейство шрифтов */
    color: #333; /* Цвет текста */
    background-color: #f8f8f8; /* Цвет фона */
    border: 1px solid #ccc; /* Рамка */
    border-radius: 4px; /* Скругление углов */
    outline: none; /* Убирает стандартное выделение */
    cursor: pointer; /* Указатель курсора */
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1); /* Тень */
    transition: border-color 0.3s, box-shadow 0.3s; /* Плавный переход для рамки и тени */
    margin: 20px;
}

/* Стили для select при фокусе */
.mySel:focus {
    border-color: #007bff; /* Цвет рамки при фокусе */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Тень при фокусе */
}

/* Стили для вариантов option */
.mySel option {
    padding: 10px; /* Внутренний отступ */
    background-color: #fff; /* Цвет фона */
    color: #333; /* Цвет текста */
}

/* Стили для выбранного option */
.mySel option:checked {
    background-color: #007bff; /* Цвет фона для выбранного варианта */
    color: #fff; /* Цвет текста для выбранного варианта */
}

/* Стили для disabled select */
.mySel:disabled {
    background-color: #e9ecef; /* Цвет фона для неактивного select */
    color: #6c757d; /* Цвет текста для неактивного select */
    cursor: not-allowed; /* Курсор для неактивного select */
}