/* Основной контейнер модального окна */
.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.0); /* Темный полупрозрачный фон */
    z-index: 1000; /* Сделать окно верхним элементом */
}

/* Активное состояние модального окна */
.modal.active {
    display: flex;
    align-items: flex-start; /* Выравнивание по верхнему краю */
    justify-content: flex-end; /* Выравнивание по правому краю */
}

/* Контент модального окна */
.modalContent {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px; /* Уменьшенный padding */
    background: papayawhip; /* Белый фон для контента */
    border-radius: 10px; /* Скругленные углы */
    min-width: 200px; /* Минимальная ширина окна */
    max-width: 300px; /* Максимальная ширина окна */
    margin: 20px; /* Отступ от краев экрана */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Легкая тень */
    animation: fadeIn 0.3s ease-out; /* Анимация появления */
}

/* Анимация для плавного появления модального окна */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px); /* Легкий сдвиг вверх */
    }
    to {
        opacity: 1;
        transform: translateY(0); /* Нормальное положение */
    }
}